.about {
  &__title {
    margin: 0 0 32px;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 48px;
      line-height: 48px;
    }

    &:before {
      content: "Plan your visit";
      display: block;
      font-weight: 700;
      font-size: 12px;
      line-height: 140%;
      letter-spacing: normal;
      color: $colorRed;
      margin-bottom: 2px;
    }
  }

  &__subtitle {
    margin: 0 0 16px;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    color: $colorDark;
  }

  &__work {
    display: flex;
    margin-bottom: 24px;
    flex-direction: column;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__hours,
  &__days {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__mode {
    margin-bottom: 16px;
    display: flex;
    gap: 24px;
  }

  &__hour {
    font-weight: 700;
  }

  &__closed-on {
    font-weight: 700;
  }
}
